@import "../../style/style.scss";

.chip-container {
  display: block;
  padding: 8px 12px;
  border-radius: 24px;

  &.chip-primary {
    background-color: $primaryMain;
    .chip-label {
      color: $white;
    }
  }

  &.chip-secondary {
    background-color: $greyBackground;
    .chip-label {
      color: $primaryDark;
    }
  }

  .chip-label {
    @extend %text;

    white-space: nowrap;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.1px;

    @include s {
      font-size: 14px;
      line-height: 14px;
    }
  }
}
